<template>
  <div class="detail">
    <header>
      {{title}}
    </header>
    <section>
      <div class="date" style="padding:24px 0 40px 0;border-bottom:1px solid #e5e5e5">
        <span style='color:#666666'>{{date}}</span>
        <span style='color:#3D93D9;margin-left:10px'>来源：{{appName}}</span>
      </div>
      <div class="pic">
         <img :src=imgurl>
      </div>
      <p v-html='content' class="content">
        {{content}}
      </p>
    </section>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      title: '',
      date:'',
      content:'',
      imgurl:'',
      appName:'',
      id:''
    }
  },
  created(){
    this.getdetail();
  },
  mounted(){
   
  },
  methods:{
   formatTime(number,format) {         //日期时间格式化
    var formateArr  = ['Y','M','D','h','m','s'];  
    var returnArr   = [];     
    var date = new Date(number * 1000);  
    returnArr.push(date.getFullYear());  
    returnArr.push(this.formatNumber(date.getMonth() + 1));  
    returnArr.push(this.formatNumber(date.getDate()));      
    returnArr.push(this.formatNumber(date.getHours()));  
    returnArr.push(this.formatNumber(date.getMinutes()));  
    returnArr.push(this.formatNumber(date.getSeconds()));     
    for (var i in returnArr)  
    {  
      format = format.replace(formateArr[i], returnArr[i]);  
    }  
    return format;  
  }, 

  //数据转化  
  formatNumber(n) {  
    n = n.toString()  
    return n[1] ? n : '0' + n  
  },
  geshi(){
    $('img').css('width','100%');
  },
 GetQueryString(name) { 
 let reg = new RegExp("(^|\\?|&)" + 'id'+ "=([^&]*)(\\s|&|$)","i");
      if(reg.test(window.location.href)){
          let idstr=unescape(RegExp.$2.replace(/\+/g," "));
          let idar=idstr.split('#');
          let id=(idar[0]);
         this.id=id;
         console.log(id);
         console.log(this.id);

        }
  },

    getdetail(){                           //获取详细信息 
      let _this=this;
      this.GetQueryString();
      console.log(_this.id);
   $.ajax({
        url:"http://47.99.138.92:8081/develop/getOneNews",      //请求的url地址
        dataType:"jsonp",   
        data:{"id":_this.id},    //参数值
          type:"POST",   //请求方式
          beforeSend:function(){
              //请求前的处理
          },
          success:function(req){
              //请求成功时处理
              console.log(req);
              _this.title=req.data.newsTitle;
              _this.appName=req.data.appName;
              let date=req.data.createDate;
              let contents=req.data.newsContent;
              if(contents.indexOf("img")!==-1){
                _this.geshi();
              }
              _this.content=contents.replace(/(\r\n|\n|\r)/gm, "<br />");
              console.log(_this.content);
              _this.imgurl=req.data.imageUrls;
              _this.date=_this.formatTime(date,'Y/M/D h:m:s');
              if(_this.imgurl==''){
                $('.pic').hide();
              }else{
                 $('.pic').show();
              }            
          },
          complete:function(){
              //请求完成的处理
          },
          error:function(){
              //请求出错处理
          }
      });

    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
header{
  text-align: left;
  font-size: 30px;
  color: #27292F;
  line-height: 48px;
  font-family: PingFangSC-Semibold, sans-serif;
}
.date{
  text-align: left;
  font-size: 24px;
}
p{
  font-size: 28px;
  color: #27292F;
  text-align: left;
  line-height: 44px;
  font-family: PingFangSC-Regular, sans-serif;
}
.pic{
  width: 100%;
  margin: 30px 0;
}
.pic>img{
  max-width: 100%;
}
p{
  text-align: justify;
}
img{
  max-width: 100%;
}
p{
  width: 90%;
  margin: auto;
}
section{
  width: 100% !important;
}
.content img{
  width: 100%;
}
</style>



